import React, { useState } from 'react';
import axios from 'axios';
import { useHistory } from 'react-router-dom';
import '../App.css';

function Login() {

    const history = useHistory();
    const [show, toggle] = React.useState(false);
    const ToggleShow = () => toggle(true);
    const ToggleHide = () => toggle(false);

    var firstName = '';
    var lastName = '';
    var emailAddress = '';
    var userName = '';
    var loginPassword = '';
    var confirmPassword = '';

    function handleClickCancel() {
        localStorage.clear();
            history.push("/");
        }

const [message, setMessage] = useState('');

const doLogin = async event => {
event.preventDefault();

var obj = { email:emailAddress.value, password:loginPassword.value };
var js = JSON.stringify(obj);

var config = {
// method: 'get',
method: 'post',
url: 'https://rate-my-city.herokuapp.com/api/login',
// url: 'https://jsonplaceholder.typicode.com/users',
headers: { 'Content-Type': 'application/json' },
data: js
};

axios(config)
.then(function (response) {
var res = response.data;
if (res.error) {
setMessage(res.error);
} else {
returnFromLogin(res[0]);
}
})

}

const doRegister = async event => {
event.preventDefault();

var obj = {
firstName: firstName.value,
lastName: lastName.value,
userName: userName.value,
email:emailAddress.value,
password:loginPassword.value,
confirmpassword: confirmPassword.value
};

var js = JSON.stringify(obj);

var config = {
method: 'post',
url: 'https://rate-my-city.herokuapp.com/api/register',
// url: 'https://jsonplaceholder.typicode.com/posts',
headers: { 'Content-Type': 'application/json' },
data: js
};

axios(config)
.then(function (response) {
var res = response.data;
if (res.error && res.error !== '') {
setMessage(res.error);
} else {
returnFromRegister(res);
}
})

}

function returnFromLogin(result) {
localStorage.setItem('accessToken', result.accessToken);
// localStorage.setItem('accessToken', '4873y47872874g7834t5t45y45y');

localStorage.setItem('userId', result.id);
localStorage.setItem('userFirstName', result.firstName);
localStorage.setItem('userLastName', result.lastName);
// localStorage.setItem('userFirstName', result.name);
// localStorage.setItem('userLastName', '');
localStorage.setItem('userName', result.userName);
localStorage.setItem('userEmail', emailAddress.value);
localStorage.setItem('userPassword', loginPassword.value);
history.push("/");
}

function returnFromRegister(result) {
// localStorage.setItem('accessToken', localStorage.accessToken);
localStorage.setItem('accessToken', result.accessToken);
localStorage.setItem('userId', result.userId);
localStorage.setItem('userFirstName', result.firstName);
localStorage.setItem('userLastName', result.lastName);
localStorage.setItem('userName', result.userName);
localStorage.setItem('userEmail', result.email);
localStorage.setItem('userPassword', loginPassword.value);
history.push("/");
}


return(
<div className="container-fluid h-custom">
<div className="row d-flex justify-content-center align-items-center h-100">
<div className="col-md-8 col-lg-6 mt-5 col-xl-4 offset-xl-1">
<form >
<div className="form-group row d-flex justify-content-center ">
<div className="col-md-4">
<button type="button" className="btn text-white btn-lg fw-bold btn-outline-dark btn-secondary" onClick={ToggleHide}>Login</button>
</div>
<div className="col-md-4">
<button type="button" className="btn text-white btn-lg fw-bold btn-outline-dark btn-secondary" onClick={ToggleShow}>Register</button>
</div>
</div>

<div className="divider d-flex align-items-center my-4 mt-0">
<p className="text-center fw-bold mx-3 mb-5"></p>
</div>

{show &&
<div className="form-outline mt-4 mb-3">
<input type="text" autoFocus
className="form-control form-control-lg"
placeholder="Enter First Name"
ref={(c) => firstName = c} />
<label className="form-label">First Name</label>
</div>
}
{show &&
<div className="form-outline mt-4 mb-3">
<input type="text"
className="form-control form-control-lg"
placeholder="Enter Last Name"
ref={(c) => lastName = c} />
<label className="form-label">Last Name</label>
</div>
}

<div className="form-outline mt-4 mb-3">
<input type="email" autoFocus
className="form-control form-control-lg"
placeholder="Enter a valid email address"
ref={(c) => emailAddress = c} />
<label className="form-label">Email Address</label>
</div>
{show &&
<div className="form-outline mt-4 mb-3">
<input type="text"
className="form-control form-control-lg"
placeholder="Enter User Name"
ref={(c) => userName = c} />
<label className="form-label">User Name</label>
</div>
}
<div className="form-outline mb-3">
<input type="password"
className="form-control form-control-lg"
placeholder="Enter password"
ref={(c) => loginPassword = c} />
<label className="form-label">Password</label>
</div>
{show &&
<div className="form-outline mt-4 mb-3">
<input type="password"
className="form-control form-control-lg"
placeholder="Confirm password"
ref={(c) => confirmPassword = c} />
<label className="form-label">Confirm password</label>
</div>
}

<div className="form-group row d-flex justify-content-center ">
{!show &&
<div className="col-md-4">
<button type="button" className="btn text-white btn-lg fw-bold btn-outline-dark btn-secondary" onClick={doLogin}>Sign In</button>
</div>
}
{show &&
<div className="col-md-4">
<button type="button" className="btn text-white btn-lg fw-bold btn-outline-dark btn-secondary" onClick={doRegister}>Sign In</button>
</div>
}

<div className="col-md-4">
<button type="button" className="btn btn-dark btn-lg form-control" onClick={handleClickCancel}>Cancel</button>
</div>
<div className="divider d-flex align-items-center my-4">
<p className="text-center fw-bold mx-3 mb-1">{message}</p>
</div>

</div>
</form>
</div>
</div>
</div>

);
};

export default Login;